import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { CustomerComponent } from './customer.component';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { AddCustomerComponent } from './add-customer/add-customer.component';
import { DetailCustomerComponent } from './detail-customer/detail-customer.component';
import { CreateDataCustomersComponent } from './create-data-customers/create-data-customers.component';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: '',
    component: CustomerComponent
  },
  {
    path: 'add',
    component: AddCustomerComponent
  },
  {
    path: 'detail',
    component: DetailCustomerComponent
  },
  {
    path: 'create-data',
    component: CreateDataCustomersComponent
  }
]

@NgModule({
  declarations: [
    CustomerComponent,
    AddCustomerComponent,
    DetailCustomerComponent,
    CreateDataCustomersComponent],
  imports: [
    CommonModule,
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    IonicModule,
    RouterModule.forChild(routes),
    TranslateModule
  ]
})
export class CustomerModule { }
