import { Component } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhImage, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { CountryCode } from 'libphonenumber-js/max';

@Component({
  selector: 'app-detail-customer',
  templateUrl: './detail-customer.component.html',
  styleUrls: ['./detail-customer.component.scss']
})
export class DetailCustomerComponent {

  detailCustomer: FormGroup;
  data: any;
  dataRestore = this.router.getCurrentNavigation().extras.state;
  customerGroups :any = [];
  /** detail c m t tab  n hay khng */
  isFromHidden: boolean;



  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhImage: VhImage,
    private vhQuery: VhQuery,
    private vhAlgorithm: VhAlgorithm,
    private functionService: FunctionService,
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
    this.isFromHidden = this.router.getCurrentNavigation().extras.state.isFromHidden;
    this.countryCode = this.data.countryCode ?? this.functionService.defaultCountryCode;
    this.callingCode = this.functionService.getCallingCodeFromCountryCode(this.countryCode) ?? this.functionService.defaultCallingCode;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.data.phone, this.countryCode as CountryCode);

    this.detailCustomer = new FormGroup({
      img: new FormControl(this.data.img),
      name: new FormControl(this.data.name, Validators.required),
      email: new FormControl(this.data.email),
      gender: new FormControl(this.data.gender),
      datebirth: new FormControl(this.data.datebirth),
      phone: new FormControl(this.formattedPhone, [Validators.required, this.functionService.phoneValidator(() => this.countryCode)]),
      address: new FormControl(this.data.address),
      country: new FormControl(this.data.country),
      province: new FormControl(this.data.province),
      district: new FormControl(this.data.district),
      note: new FormControl(this.data.note),
      debt_enable: new FormControl(this.data.debt_enable),
      id_customer_class : new FormControl(this.data.id_customer_class ? this.data.id_customer_class : '' ),
      type : new FormControl(this.data.type),
      id_branch : new FormControl(this.data.id_branch),
      time_for_payment: new FormControl(this.data.time_for_payment),
      delete_hidden: new FormControl(this.data.delete_hidden),
      isBusiness: new FormControl(this.data.isBusiness),
      taxcode: new FormControl(this.data.taxcode ),
    });
    // taxcode required khi isBusiness = true
    if(this.data.isBusiness) {
      this.detailCustomer.get('taxcode').setValidators([Validators.required]);
      this.detailCustomer.get('taxcode').updateValueAndValidity();
    }
  }



  ngOnInit(): void {
    this.customerGroups = this.vhAlgorithm.sortStringbyASC(this.vhQueryCafe.getlocalCustomerClasss(), 'name')
  }



  /**
   * tr v trang danh sch khch hng
   * @example this.goBack()
   * 
   */
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/partner/customer'], {
      state: this.dataRestore
    });
  }



  /**
   * xa khch hng khi DB
   * @example this.deleteCustomer()
   */
  deleteCustomer() {
    this.vhQueryCafe.deleteCustomer(this.data._id)
      .then(bool => {
        if (bool) {
          if (this.detailCustomer.value["img"]) {
            this.vhImage.deleteThumbnail([this.detailCustomer.value["img"]]).then(() => {
              this.vhComponent.showToast(2000, (this.languageService.translate("Customer has been deleted successfully")), 'success-toast');
              setTimeout(() => {
                this.isVisible = false;
                this.goBack();
              }, 300);
            })
          } else {
            this.vhComponent.showToast(2000, (this.languageService.translate("Customer has been deleted successfully")), 'success-toast');
            setTimeout(() => {
              this.isVisible = false;
              this.goBack();
            }, 300);
          }
        } 
        else {
          // this.vhComponent.showToast(2000, (this.languageService.translate("Can't delete this customer")), 'alert-toast');
          this.isVisible = false;
          this.vhComponent.showToast(4000, this.languageService.translate('You cannot delete this customer because it has generated invoices, but you can hide it and only see it in the reports.'), 'alert-toast');
          // this.functionService.confirmUpdateHidden('customer').then(() => {
          //   this.vhQueryCafe.updateCustomer(this.data._id, { delete_hidden: true }).then(() => {
          //     this.vhComponent.showToast(3000, (this.languageService.translate('Successfully hidden!')), 'success-toast');
          //     setTimeout(() => {
          //       this.goBack();
          //     }, 300);
          //   }).catch((err) => { 
          //     console.log(err);
          //   });
          // }).catch(() => { });
        }
      });
  }
 

 
  /**
   * x l thay i trng cho php n ca khch hng
   * @param value 
   * @example this.changeDebtEnable(true)
   */
  changeDebtEnable(value) {
    this.detailCustomer.value.debt_enable = value;
    this.updateCustomer({debt_enable: this.detailCustomer.value.debt_enable});
    if(value)   this.detailCustomer.addControl('time_for_payment', new FormControl(0, [Validators.required]))
    else    this.detailCustomer.removeControl('time_for_payment');
  }



  /**
   * x l thay i trng ngy sinh ca khch hng
   * @param value 
   * @example this.changeDebtEnable(true)
   */
  onChangeDatePicker(value) {
    this.detailCustomer.value.datebirth = value;
    this.updateCustomer({datebirth: this.detailCustomer.value.datebirth});
  }



  /** ------ Modal Zorro---- */
  isVisible = false;



  /**
   * m modal xc nhn xa khch hng
   * @example this.showModal()
   */
  showModal(): void {
    this.isVisible = true;
  }



  /**
   * bt sk nhn nt ok modal xc nhn xa khch hng
   * @example this.showModal()
   */
  handleOk(): void {
    this.deleteCustomer();
  }



  /**
   * tt modal xc nhn xa khch hng
   * @example this.showModal()
   */
  handleCancel(): void {
    this.isVisible = false;
  }

  

  isExitsPhone: boolean = false;
  /**
   * cp nht khch hng theo trng truyn vo
   * @param value 
   * @example this.updateCustomer({name : 'nguyen van a'})
   */
  updateCustomer(value) {
    if (this.detailCustomer.valid) {
      let customer = { ...value };
      if(value.phone) {
        customer.countryCode = this.countryCode;
        customer.phone = this.functionService.getNationalPhoneNumber(customer.phone, this.countryCode);
        customer.phoneNumber = this.functionService.getInternationalPhoneNumber(customer.phone, this.countryCode);
      }
      console.log(customer);
      
      this.vhQueryCafe.updateCustomer(this.data._id, customer).then((rsp:any) => {
        if(rsp.vcode == 0) this.isExitsPhone = false;
        else if(rsp.vcode == 1) {
         //This phone number is already in use with another customer
          this.isExitsPhone = true;
          this.vhComponent.showToast(1500, this.languageService.translate("This phone number is already in use with another customer"), 'alert-toast')
        }
      }, (error:any) => {
        console.log('error', error);
      })
    }
  }



  /**
   * m folder chn file
   * @example this.getFile()
   */
  getFile() {
    document.getElementById("file-upload").click();
  }



  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "customers", this.detailCustomer.value["img"] || '').then(
      photoURL => {
        this.detailCustomer.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), 'success-toast');
        this.updateCustomer({img: this.detailCustomer.value.img});
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), 'success-toast');
      }
    );
  }
  
  
  
  /** M quc gia */
  countryCode: string;
  /** Calling code */
  callingCode: string;
  /** S in thoi  format. VD: 333 333 333 */
  formattedPhone: string;

  /**
   * M modal chn m quc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.detailCustomer.value.phone, this.countryCode as CountryCode);
      this.detailCustomer.get('phone')?.markAsTouched();
      this.detailCustomer.get('phone')?.markAsDirty();
      this.detailCustomer.get('phone')?.updateValueAndValidity();
      this.updateCustomer({phone: this.detailCustomer.value.phone});
    }).catch((error) => {
      // console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format s in thoi khi nhp v set vo form
   * @param event S kin khi nhp s in thoi
   */
  formatPhoneNumber(event: Event): void {
    // Ly s in thoi  format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cp nht vo form
    this.detailCustomer.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

}
